.mob-img {
    width: 100%;
    height: 91vh;
    background: url(https://assets2.razerzone.com/images/pnx.assets/91baf798ef1e551ef8d4e850c28a0050/razer-edge-1920x700-desktop-cat-banner.jpg) no-repeat center center/cover;
    display: flex;
    justify-content: center;
    padding-top: 60px;
    color: azure;
    font-family: 'Roboto', sans-serif;
}

.img-mob111 {
    text-align: center;
    padding: 0 20px;
}


.img-mob111 a {
    color: #44d62c;
    font-size: 20px;
    font-weight: 300;
}

.img-mob111 h1 {
    font-size: 30px;
    font-weight: 300;
}

.img-mob111 p {
    font-size: 20px;
    font-weight: 300;
    margin-top: 15px;
    margin-bottom: 15px;
}


.mob-p1 a {
    color: #fff;
}

.mob-p1 {
    width: 100%;
    height: 60px;
    background: #555;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.mobileHeader {
    font-family: 'Roboto', sans-serif;
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #111;
}

.centerT {
    text-align: center;
    padding: 0 20px;
}

.centerT h1 {
    color: #44d62c;
    font-weight: 300;

}

.centerT p {
    color: #999;
}

.mobile_parent {
    width: 100%;
    height: max-content;
    padding: 60px 0;
}

.cards {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    font-family: 'Roboto', sans-serif;
}

.cardd {
    background: #111;
    border-bottom: 1px solid #44d62c;
    height: -webkit-max-content;
    height: max-content;
    padding-bottom: 10px;
}


.cardd img {
    object-fit: cover;
    width: 100%;
}

.cardd h2 {
    min-height: 44px;
    color: #fff;
    font-weight: 300;
    padding: 20px;
    font-size: 20px;
}

.cardd p {
    color: #999;
    font-size: 14px;
    min-height: 104px;
    padding: 10px 15px;
}

.cardd a {
    color: #44d62c;
    padding: 0 15px
}


.sec-img{
    width: 100%;
    height: 69vh;
    background: url(https://assets2.razerzone.com/images/pnx.assets/8d15cb8a565e71ef01f047785d22ce69/gothe-extramile-category-1920x700-desktop.jpg) no-repeat center center/cover;
    justify-content: center;
    padding-top: 60px;
    margin-top: 30px;
    color: azure;
    font-family: 'Roboto', sans-serif;
}


.sec-img a {
    color: #44d62c;
    font-size: 20px;
    font-weight: 300;
}

.sec-img h1 {
    font-size: 30px;
    font-weight: 300;
}

.sec-img p {
    font-size: 20px;
    font-weight: 300;
    margin-top: 15px;
    margin-bottom: 15px;
    color: #999;
}


.sec1-img {
    text-align: center;
}



