* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}

.otadiv {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

body {
  background: #000000;
}

.otadiv button {
  background: #141414;
}

.card {
  width: 280px;
  height: max-content;
  padding: 30px;
  text-align: center;
  padding: 10px 15px;
  background: #141414;
}

.card img {
  background: #111;
  object-fit: cover;
  width: 100%;
}

input {
  margin-left: 400px;
  width: 50%;
  height: 50px;
  padding-left: 20px;
  background: #111;
}