.section-3 {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.left {
    width: 100%;
    height: 80vh;
    border-top: 10px solid #393838;
    display: flex;
    gap: 20px;
    font-size: 20px;
    justify-content: center;
    color: #fff;
    padding-top: 50px;
    background: url("https://assets2.razerzone.com/images/pnx.assets/84d0946021d11b959bda0545c4d4c234/razer-blade14-quartz-2022-950x580-desktop.jpg") no-repeat center center/cover;
}

.left-1 h3 {
    font-size: 20px;
    text-align: center;
    padding-top: 10px;
    font-weight: 300;
}


.left1-left1 a {
    color: #fff;
    background: transparent !important;
    padding-top: 10px;
    font-size: 20px;
    font-weight: 300;
    margin-left: 30px;
}

.right {
    width: 100%;
    height: 80vh;
    border-top: 10px solid #393838;
