@media screen and (max-width: 872px ) {
    .section-3 {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    .right-1 {
        border-left: 0;
    }

    .right {
        border-left: 0px solid #393838;
    }
    .right2 {
        border-left: 0px solid #393838;
    }
    .right3 {
        border-left: 0px solid #393838;
    }
