.stor a {
    color: #fff;
}

.stor-p p {
    width: 100%;
    height: 60px;
    background: #555;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Stor h1{
    border-bottom: 4px solid #44d62c;
}


.stor-img {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 30px;
    margin-bottom: 100px;
    overflow-x: scroll;
}


.stor-img::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    /* width of the entire scrollbar */
}

.stor-img::-webkit-scrollbar-track {
    background: #000;
    /* color of the tracking area */
}

.stor-img::-webkit-scrollbar-thumb {
    background-color: #44d62c;
    /* color of the scroll thumb */
    border-radius: 20px;
    /* roundness of the scroll thumb */
    border: 3px solid #44d62c;
    /* creates padding around scroll thumb */
}

.stor-img img {
    width: 100px;
    height: 100px;
    object-fit: contain;
}

.stor-img img:hover {
    border-bottom: #44d62c 2px solid;
}

.store_parent {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    padding: 40px 0;
    font-weight: 300;
}

.store_card {
    width: 100%;
    background: #333;
}

.store_card img {
    width: 100%;
    background: #111;
}

.desc {
    min-height: 60px;
    color: #888;
    font-size: 14px;
    font-weight: 400;
    margin-top: 30px;
    padding: 10px;
}
.Cancel-btn {
    background: red !important;
    color: #fff !important;
}

.price {
    display: block;
    font-size: 1em;
    margin-block-start: 1.67em;
    margin-block-end: 1.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
}

.store_card h3 {
    min-height: 70px;
    font-weight: 400;
    padding: 10px;
}

.store_card button {
    background: #44d62c;
    border: none;
    border-radius: 6px;
    color: #000;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
    height: 25px;
    transition: .5s ease;
    width: 60px;
}


.store-de-bt {
    height: 50px;
    align-items: center;
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
}


.loading {
    width: 100%;
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    padding: 100px 0;
}


@media screen and (max-width: 1100px) {
    .store_parent {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
    }
}


@media screen and (max-width: 852px) {
    .store_parent {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 600px) {
    .store_parent {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
}


