* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  text-decoration: none;
  list-style: none;
}

.container {
  width: 90%;
  margin: auto;
}

body {
  background: #393838;
  font-family: 'Roboto', sans-serif;
}


.bigger {
  color: #fff;
}

.Header-wrap .bigger {
  font-size: 50px;
  text-align: center;
}


.header-imag {
  width: 100%;
}



.big {
  color: #fff;
  text-align: center;
  font-weight: 300;
}


.Header .big {
  font-size: 30px;
  font-weight: 300;
}


.header-btn {
  color: #fff;
}


.Header .header-btn {
  font-size: 40px;
  border: none;
  background: transparent;
}

.header-btn2 {
  color: #fff;
}


.Header .header-btn2 {
  font-size: 40px;
  border: none;
  background: transparent;
}

.Header {
  width: 100%;
  height: 91vh;
  display: flex;
  justify-content: center;
  padding-top: 90px;
  line-height: 50px;
  background: url(https://assets2.razerzone.com/images/pnx.assets/0e80e38b843acf6f0b5b237456108a2d/razer-deathadder-v3-pro-hero-desktop.jpg) no-repeat center center/cover;
}


.sec1 {
  width: 100%;
  height: 100vh;
  background: url(https://assets2.razerzone.com/images/pnx.assets/d24672112ff237ece31fd792eb39dba6/razer-enki-pro-koenigsegg-williams-esports-homepage-large.jpg) no-repeat center center/cover;
  margin-top: 15px;
}



.sec1-btn {
  background: #fa9a01;
  cursor: pointer;
  text-align: start;
  width: 75px;
  font-size: 10px;
  height: 50px;
}


.header-btn span {
  color: #44d62c;
}


.header-btn2 span {
  color: #44d62c;
}

.sec1-btn {
  width: 100px;
  height: 50px;
  border: none;
  padding: 0 11px;
}



.twink {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.section1 {
  width: 100%;
  text-align: center;
  justify-content: center;
  padding-top: 50px;
}


.sec1-btn h5 {
  font-size: 15px;
}

.sec-1h1 h1 {
  color: #fff;
  font-size: 50px;
  letter-spacing: 3px;
  margin-top: 30px;
  font-family: 'Roboto', sans-serif;
}

.sec-1h1 h2 {
  color: #fff;
  font-size: 30px;
  font-weight: 300;
  margin-top: 15px;
}

.sec1-sec-1-btn1 a {
  background: transparent;
  color: #fff;
  border: none;
  font-size: 30px;
  font-weight: 400;
}


.sec1-btn p {
  font-size: 10px;
}


.sec1-sec-1-btn1 span {
  color: #44d62c;
  font-size: 32px;
  font-weight: 400;
}


@media screen and (max-width: 1100px) {
  .store_parent {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
  }
}


@media screen and (max-width: 852px) {
  .Header {
      width: 100%;
      display: grid;
  }
}

@media screen and (max-width: 600px) {
  .Header {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
  }
}