.fixed-nav {
    position: sticky;
    background: #000;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
  }
  .navbar-container {
    width: 90%;
    margin: auto;
    padding: 2vh 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    .navbar-burger {
      img {
        cursor: pointer;
      }
    }
    .navbar-logo {
      display: flex;
      width: 35px;
      height: 35px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        display: flex;
      }
    }
  
    .navbar-links {
      display: flex;
      gap: 50px;
      .user i {
        font-size: 20px;
        font-weight: normal;
      }
      a {
        font-size: 16px;
        font-weight: 500;
        color: #a9a8a8;
        cursor: pointer;
  
        &:hover {
          color: white;
        }
      }
    }
  
    .navbar-cart {
      display: flex;
      width: fit-content;
      align-items: center;
      padding-top: 10px;
      height: fit-content;
      cursor: pointer;
      gap: 20px;
  
      .storelink {
        width: 25px;
      }
  
      span {
        background: #44d62c;
        color: #111111;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 20px;
        margin-left: 12px;
        border-radius: 50%;
        font-size: 13px;
        font-weight: bold;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }

  .storelink{
    display: flex;
  }
  .storelink sub {
    position: relative;
    bottom: 10px;
    color: #44d62c;
    font-size: 18px;
  }
  
  .log_out{
    background: #44d62c;
    border-radius: 5px;
    color: #fff !important;
    padding: 0 7px;
  }
  
  @media all and (min-width: 1280px) {
    .navbar-container {
      max-width: 1146px;
      margin: 0 auto;
    }
  }
  
  @media all and (min-width: 750px) {
    .navbar-container {
      .navbar-burger {
        display: none;
      }
    }
  }
  
  @media all and (max-width: 750px) {
    .navbar-container {
      padding: 0 2vh;
      .navbar-links {
        display: none;
      }
    }
    .navbar-cart {
      span {
        top: 12px !important;
      }
    }
  }
  


  @media all and (min-width: 750px) {
      .modern-draw {
        display: none;
      }
  }




  .modern-draw {
    background: #000;
    border: none;
  }

  #drawer{
    background-color: black !important;
  }

  .bla{
    background-color: black !important;
  }


  .nav-modern {
    text-align: center;
    justify-content: center;
    margin-top: 150px;
  }
  .nav-modern #mobile {
    border-bottom: 2px solid #44d62c !important;

  }