footer {
    background: #111;
    border-top: 4.5px solid #393838;
    height: max-content;
    width: 100%;
}

footer ul {
    width: 100%;
    display: grid;
    padding: 40px 0;
    grid-template-columns: repeat(5, 1fr);
    border-bottom: 2px solid gray;
}

footer li {
    padding:  15px 0;
}


footer li p {
    font-size: 14px;
    padding: 10px 0;
    color: #a9a8a8;
    cursor: pointer;
}

footer li p:hover{
    color: #fff;
}

footer li img{
    width: 60px;
}


.ft-img{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    gap: 15px;
    color: #44d62c;
}

.ft-p-p {
    width: 100%;
    display: flex;
    color: #a9a8a8;
    padding: 20px 0;
    justify-content: space-between;
}

@media all and (max-width:1216px ) {
    footer ul {
        width: 100%;
        display: grid;
        padding: 40px 0;
        grid-template-columns: repeat(4, 1fr);
        border-bottom: 2px solid gray;
    }
}


@media all and (max-width:964px ) {
    footer ul {
        width: 100%;
        display: grid;
        padding: 40px 0;
        grid-template-columns: repeat(3, 1fr);
        border-bottom: 2px solid gray;
    }
}

@media all and (max-width:768px ) {
    footer ul {
        width: 100%;
        display: grid;
        padding: 40px 0;
        grid-template-columns: repeat(2, 1fr);
        border-bottom: 2px solid gray;
    }
    footer ul li{
        text-align: center;
    }
}


@media all and (max-width:500px ) {
    footer ul {
        width: 100%;
        display: grid;
        padding: 40px 0;
        grid-template-columns: repeat(1, 1fr);
        border-bottom: 2px solid gray;
    }
    footer ul li{
        text-align: center;
    }
}